.float-container {
  position: relative; top: 0; right: 0; left: 0; bottom: 0; height: 300px;
 
  
}

.float-circle {
 
 position: absolute;
 padding: 16px 12px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
  transform: translatey(0px);
  animation: float 6s ease-in-out infinite;

  
}

@keyframes float {
  0% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translatey(0px);
  }

  50% {
    box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
    transform: translatey(-20px);
  }

  100% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translatey(0px);
  }
}


/* FLOATING BLOB STYLES */

.float-blob-left {position: absolute; height: 420px; width: 420px; z-index: -1; top: -200px; left: 100px; border-radius: 50%; border: 25px solid #f9fbfd; background-color: transparent;}

.float-blob-right {position: absolute; height: 820px; width: 820px; z-index: -1; top: 100px; right: 0px; border-radius: 50%; border: 50px solid #f9fbfd; background-color: transparent;}

.blob-container {position: absolute; right: 0; left: 0; }


